
import XTable from 'vux/src/components/x-table/index.vue'

import Bus from '../eventbus'
export default {
name: 'detail',
components: {
XTable
},
data () {
return {
detail: []
}
},
created () {
let vm = this;
let name = vm.$route.query.name;
vm.$ajax.get('static/detail.json', {
params: {
name: name
}
})
.then(function(res){
console.log('res: ',res);
res.data.forEach(function(item, index){
if(item.name == name){
console.log('item: ',item);
item.time = getLocalTime(item.time);
vm.detail.push(item);
}
})
})
.catch(function(err){
console.log('err: ',err);
})

Bus.$on('pagedata', data => this.pagedata = data);
Bus.$on('page', data => this.page = data);
},
methods: {},
beforeDestroy() {
Bus.$emit('pagedata', this.pagedata);
Bus.$emit('page', this.page);
},
destroyed() {
Bus.$off('pagedata');
Bus.$off('page');
}
}




